@import url(https://fonts.googleapis.com/css2?family=Marck+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
   
    margin: 1rem;
    max-width: 960px;
    margin: 0 auto;
    background-color: black;

  }
 .loginContainer{
  background-color: #3D6FAD ;
  padding: 1rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  -webkit-flex-grow: 1;
          flex-grow: 1;
 }
 .appimg{
  max-width: 350px;
  display: block;
  margin: auto;
 }
 .loginContainer h1{
  font-size: 3rem;
  color: white;
  text-align: center;
  
 }
 .loginContainer p{
  font-family: 'Marck Script', cursive;
  color: white;
  text-align: center;
  font-size: 1.5rem;
  font-weight: bolder;
  background-color: #88AEDB;
  border-radius: 5px;
  max-width: 650px;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem;
  
 }

 .loginbtn{
  border: none;
  background-color: orange;
  border-radius: 50px;
  margin: 1rem;
  padding: 1rem;
  outline: none;
  cursor:pointer;
  width: 250px;
  color: white;
  font-size: large;
  margin-left: auto;
  margin-right: auto;
}
 header{
     position: fixed;
     top: 0;
     background-color: #3D6FAD ;
     max-width: 960px;
     color: white;
     width: 100%;
     border-top-left-radius: 10px;
     border-top-right-radius: 10px;
     height: 10vh;
     min-height: 50px;
     
 }
 header h1{
   padding-left: 1rem;
 }
.round-picture{
    border-radius: 50px;
    width: 50px;
    height: 50px;
    padding: 1rem;
}
.message {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 1rem;
  }
  .sent {
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
  }
.sent p{
    background-color: #3D6FAD ;
    color: white;
    font-weight: bold;
    padding: 1rem;
    border-radius: 10px;
}
.received p{
    background-color: #374151;
    color: white;
    font-weight: bold;
    padding: 1rem;
    border-radius: 10px;
}
.msginput {
    line-height: 5em;
    width: 100%;
    font-weight: bold;
    background-color: transparent;
    color: white;
    outline: none;
    border: none;
    padding: 0 10px;
    -webkit-flex-grow:2;
            flex-grow:2;
  }
  .msgform {
    height: 10vh;
    position: fixed;
    bottom: 0;
    background: #374151;
    width: 100%;
    max-width: 945px;
    display: -webkit-flex;
    display: flex;
    font-size: 1.5rem;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding: 2px;
    padding: 0 10px;
    border-radius: 10px;
    
  }
   .sendbtn{
     border: none;
     background-color: orange;
     border-radius: 50px;
     margin: 1rem;
     padding: 1rem;
     outline: none;
     cursor:pointer;
 }
    
 .scroll{

    padding: 10px;
    height: 80vh;
    margin: 10vh 0 10vh;
    overflow-y: scroll;
    overflow-anchor: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    
  } 

  .backdrop{
  
    margin: auto;
    background: #374151;
    padding: 10px;
    height: 90vh;
    margin: 10vh 0 10vh;
    overflow-y: scroll;
    overflow-anchor: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
   
  }
  .hide{
    display:none;
  }
  .listOfRooms{
    margin: auto;
    padding: 1rem;
    font-size: medium;
    color: white;
    
  }
  .addbtn{
    background-color: orange;
    color: white;
    font-size: 2rem;
    font-weight: bolder;
    border: none;
    outline: none;
    border-radius: 50%;
    max-width: 8%;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
  }
  .createForm{
    margin-left: auto;
    margin-right: auto;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  .createForm input{
    padding: 1rem;
    border: 3px solid orange;
    border-radius: 4px;
    outline: none;
    margin-bottom: 1rem;
    

  }
  .saveBtn{
    margin-right: 8rem;
    border: none;
    outline: none;
    background-color: rgb(29, 194, 29);
    color: white;
    padding: 0.5rem;
    cursor: pointer;
    border-radius: 3px;

  }
  .cancelBtn{
   
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
  }
